'use server';
import { z } from 'zod';

const contactSchema = z.object({
  name: z.string().min(2, { message: "Name must be at least 2 characters." }),
  email: z.string().email({ message: "Invalid email address." }),
  message: z.string().min(10, { message: "Message must be at least 10 characters." }),
});

export interface ContactFormState {
  message: string;
  errors?: {
    name?: string[];
    email?: string[];
    message?: string[];
    server?: string[];
  };
  success: boolean;
}

export async function submitContactForm(
  prevState: ContactFormState,
  formData: FormData
): Promise<ContactFormState> {
  const validatedFields = contactSchema.safeParse({
    name: formData.get('name'),
    email: formData.get('email'),
    message: formData.get('message'),
  });

  if (!validatedFields.success) {
    return {
      errors: validatedFields.error.flatten().fieldErrors,
      message: "Failed to send message. Please check the fields.",
      success: false,
    };
  }

  const { name, email, message } = validatedFields.data;

  const sheetId = "-1ORtBI77_8eujwQCrV7CgEH-wAwysUDfXuwAR-Jwl0b8";
  const apiUrl = `https://sheet.best/api/v1/${sheetId}`;

  try {
    const response = await fetch(apiUrl, {
      method: "POST",
      mode: 'cors',
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name,
        email,
        message,
      }),
    });

    if (!response.ok) {
      console.error("Failed to send data to Google Sheet:", response.status, response.statusText);
      return {
        message: "Failed to send message. Please try again later.",
        errors: { server: ["Failed to send data to Google Sheet"] },
        success: false,
      };
    }

    const data = await response.json();
    console.log("Data sent to Google Sheet:", data);

  } catch (error) {
    console.error("Error sending data to Google Sheet:", error);
    return {
      message: "An unexpected error occurred. Please try again later.",
      errors: { server: ["Error sending data to Google Sheet"] },
      success: false,
    };
  }

  // Simulate email sending delay
  await new Promise(resolve => setTimeout(resolve, 1000));

  // Simulate a potential server error (uncomment to test error handling)
  // if (Math.random() > 0.5) {
  //   return {
  //     message: "An unexpected error occurred on the server. Please try again later.",
  //     errors: { server: ["Server simulation error"] },
  //     success: false,
  //   };
  // }

  return {
    message: "Your message has been sent successfully! We'll get back to you soon.",
    errors: {},
    success: true,
  };
}
